/* 컬러 변수 */
:root {
  /* 포인트컬러 */
  --primary-color: #0171bb;
  --secondary-color: #2c4162;
  /* 글자 공통 컬러 */
  --text-color-100: #fff;
  --text-color-200: #eee;
  --text-color-300: #ddd;
  --text-color-400: #ccc;
  --text-color-500: #666;
  --text-color-600: #222;
  --text-color-900: #000;
}

ol, ul {
  list-style: none;
}

img {
  width: 88px;
  height: 31px;
}

/* 후기 */
.App .btn {
  padding: 10px 0;
}
.btn_wrap {
  display: flex;
}
.review_wrap {
  margin-bottom: 80px;
}
.review_wrap .review_cnt {
  line-height: 80px;
}
.review_wrap .grade_wrap {
  padding-top: 40px;
  margin: 0 auto 60px;
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.review_wrap .grade_wrap .left {
  text-align: center;
}
.review_wrap .grade_wrap .left strong {
  font-size: 30px;
  font-weight: 700;
  display: block;
}
.review_wrap .grade_wrap .left strong em {
  font-weight: 400;
  color: var(--text-color-400);
}
.review_wrap .grade_wrap .right li {
  display: flex;
  gap: 0 10px;
}
.review_wrap .grade_wrap .right li + li {
  margin-top: 10px;
}
.review_wrap .grade_wrap .right span {
  text-align: left;
  font-weight: 700;
}
.review_wrap .grade_wrap .right .graph {
  width: 200px;
  background: #ddd;
  border-radius: 20px;
  position: relative;
}
.review_wrap .right .graph::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 93%;
  height: 100%;
  background: var(--primary-color);
  border-radius: 20px;
}
.review_wrap .right li:nth-child(2) .graph::after {
  width: 90%;
}
.review_wrap .right li:nth-child(3) .graph::after {
  width: 86%;
}
.review_wrap .right li:nth-child(4) .graph::after {
  width: 83%;
}

.review_wrap .review_list_wrap {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding-bottom: 50px;
}
.review_wrap .review_list_wrap li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.review_wrap .review_list_wrap li .profile_img_wrap {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: #ddd;
  align-self: flex-start;
}
.review_wrap .review_list_wrap li + li {
  border-top: 1px solid #ddd;
}
.review_wrap .review_list_wrap li .review_info_wrap {
  margin: 0 auto 0 10px;
  text-align: left;
}
.review_wrap .review_list_wrap li .review_info_wrap span {
  display: block;
  font-weight: 700;
}
.review_wrap .review_list_wrap li .review_info_wrap strong {
  display: inline-block;
  margin-left: 5px;
  font-weight: 700;
}
.review_wrap .review_list_wrap li .review_info_wrap em {
  color: #666;
}
.review_wrap .pagination {
  padding-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.review_wrap .pagination span {
  display: block;
  background: #eee;
  height: 30px;
  padding: 0 10px;
  border-radius: 4px;
}
.review_wrap .pagination .num_btn {
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  position: relative;
  background: #eee;
}
.review_wrap .pagination .num_btn::after {
  content: '';
  display: block;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%) rotateZ(-90deg);
}
.grade {
  width: 95%;
  text-align: center;
  border: 1px solid grey;
  border-radius: 5px;
}